                    </div>
                    <i class="iconfont fs-calender"></i>
                    <div class="brand">{{house_detail.brand}}</div>
                </div>
                <!-- current -->
                <div v-else-if="moment === 'current'"
                    class="cover current">
                    <div class="logo">logo</div>
                    <div class="btn-wrap">
                        <div class="info">
                            <div>{{house_detail.main_title.type}}</div>
                            <div>{{house_detail.main_title.brand}}</div>
                            <div>{{house_detail.main_title.time}}</div>
                        </div>
                        <div class="btn">
                            <van-button type="primary"
                                @click="handle_to_see_webcast">
                                <span style="margin-right:20px">观看直播</span>
                                <i class="iconfont fs-online-video"></i>
                            </van-button>
                        </div>
                    </div>
                </div>
                <!-- playing -->
                <div v-else-if="moment === 'playing'"
                    class="cover playing">
                    <div class="logo">{{house_detail.logo}}</div>
                    <div class="video-wrap">
                        <video class="video"
                            controls="true"
                            width="100%"
                            height="100%"
                            webkit-playsinline
                            playsinline>
                            <source src="http://ivi.bupt.edu.cn/hls/cctv1hd.m3u8" />
                            <!-- <source src="http://192.168.81.38:8081/download/video/01Log in.mp4" /> -->
                        </video>
                    </div>
                    <div class="btn-wrap">
                        <div class="info">
                            <div>{{house_detail.main_title.type}}</div>
                            <div>{{house_detail.main_title.brand}}</div>
                            <div>{{house_detail.main_title.time}}</div>
                        </div>
                    </div>
                </div>
                <!-- after -->
                <div v-else-if="moment === 'after'"
                    class="show-time before">
                    <div>
                        <font>{{house_detail.show_time_day}}</font>
