</template> 
   
<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
import fetch from "@/services/fetch";

export default {
  name: "",
  components: {},
  /*父组件传入的属性的类型*/

  props: {},

  data() {
    return {
      moment: "before",
      timer: "",
      img_path: "http://placehold.it/500x500/000000",
      first_screen_height: "", // 首屏的高度，直播前后和直播中不一样
      keywords: "",
      introduce_video_show: false,
      house_detail: {
        logo: "这是我的图片logo",
        brand: "Dior", // 直播的品牌
        main_title: {
          // 直播开始或者当中，品牌的宣传文案
          type: "生活",
          brand: "DIOR",
          time: "2020-2021 春夏成衣"
        },
        show_start_time: "2020-6-12 15:23", // 直播开始时间
        show_time_day: "2020年7月9日的时装秀", // 直播开始展示时间（类似标题）
        show_time_hour: "14:30", // 直播开始展示时间（时分）
        show_end_time: "2020-6-22 15:24", // 直播结束时间
        introduce:
          "克里斯汀·迪奥于1905年出生于法国的诺曼底，金色后来也成了Dior品牌最常见的代表色。克里斯汀·迪奥并不是服装设计出身，毕业于巴黎政治学院，又作为企业家之子，他对艺术的热情却从未消退。1946年，在时尚领域不断浮沉后，已经不惑之年的克里斯汀·迪奥才在巴黎Montaigne大道开了第一家个人服饰店",
        media_contact: "show@dioronline.com",
        video_path: "",
        showroom_contact: "srouleau@pressonline.com",
        recommend_style: "看看书", // 介绍正文，推荐标题或风格
        recommend_time: "2020-2021 春夏成衣" // 介绍正文，推荐标题或风格
      },
      link_list: [
        {
          icon: "fs-weibo",
          link: "1"
        },
        {
          icon: "fs-weixin",
          link: "2"
        },
        {
          icon: "fs-kongjian",
          link: "3"
        }
      ],
      news_list: [
        {
          id: 1,
          img_path: "http://placehold.it/300x200/1d00fa",
          time: "2020年7月9日",
          title: "这是一段新闻的标题",
          content: "这是一段新闻的内容这是一段新闻的内容这是一段新闻的内容" // 此内容不可过长
        },
        {
          id: 2,
          img_path: "http://placehold.it/300x200/1d00fa",
          time: "2020年7月9日",
          title: "这是一段新闻的标题",
          content: "这是一段新闻的内容这是一段新闻的内容这是一段新闻的内容这是一段新闻的内容这是一段新闻的内容这是一段新闻的内容"
        }
      ]
    };
  },

  computed: {},
  watch: {},

  created() {},
  mounted() {
    this.check_moment();
    window.addEventListener("onorientationchange" in window ? "orientationchange" : "resize", this.orientate, false);
  },

  methods: {
    check_moment() {
      clearInterval(this.timer);
      const start_time = Math.floor(new Date(this.house_detail.show_start_time).getTime() / 1000);
      const end_time = Math.floor(new Date(this.house_detail.show_end_time).getTime() / 1000);
      const count_start_seconds = start_time - Math.floor(new Date().getTime() / 1000);
      const count_end_seconds = end_time - Math.floor(new Date().getTime() / 1000);
      if (count_start_seconds <= 0) {
        if (count_end_seconds <= 0) {
          this.moment = "after";
          this.first_screen_height = "";
          return false;
        } else {
          if (this.moment !== "playing") {
            this.moment = "current";
          }
          this.first_screen_height = "height: calc(100vh - 80px)";
        }
      }
      this.timer = setInterval(() => {
        this.check_moment();
      }, 1000);
    },
    // 观看直播
    handle_to_see_webcast() {
      this.moment = "playing";
    },
    // 观看品牌介绍视频
    handle_play_introduce_video() {
      this.introduce_video_show = true;
    },
    handle_close_introduce_video() {
      this.introduce_video_show = false;
    },
    // 下载介绍PDF
    handle_download_introduce_pdf() {
      console.log("xaizai");
    },
    // 处理横屏、竖屏事件
    orientate() {
      if (window.orientation === 90 || window.orientation === -90) {
        // alert("cross");
      } else {
        // alert("vertical");
      }
    }
  }
};
</script> 
