<template>
    <div class="index-list">

        <van-collapse v-model="activeNames">
            <van-collapse-item v-for="(list,index) in lists"
                :key="index"
                :ref="'collapseItem_'+list.index"
                :title="list.index"
                :name="list.index"
                :class="$getSplashType()">
                <HouseListInline :values="list.values"></houseListInline>

                <span slot="right-icon"></span>
            </van-collapse-item>
        </van-collapse>

        <div v-if="activeIndex"
            class="index-anchor-top">{{activeIndex}}</div>

        <index-coordinate :active="activeIndex"
            @jump:index="jump_to_index"></index-coordinate>
    </div>
</template> 
   
<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
import fetch from "@/services/fetch";
import IndexCoordinate from "./index-coordinate";

export default {
  name: "IndexList",
  components: { IndexCoordinate },

  // mixins:[混入的组件],

  /* 在组件设置 v-model时修改默认value和input */

  model: {
    prop: "checked", //注意当自定义v-mode的prop后，在下面 props中还要显式声明 类型等
    event: "change"
  },

  data() {
    return {
      activeNames: ["A", "B", "C"],

      activeIndex: "", //触顶后，显示的索引

      lists: [
        {
          index: "A",
          values: [
            {
              id: 1,
              title: "AALTO",
              imgurl: "http://placehold.it/300x200/1d00fa"
            },
            {
              id: 2,
              title: "AALTO",
              imgurl: "http://placehold.it/300x200/1d00fa"
            },
            {
              id: 3,
              title: "AALTO",
              imgurl: "http://placehold.it/300x200/1d00fa"
            },
            {
              id: 4,
              title: "AALTO",
              imgurl: "http://placehold.it/300x200/1d00fa"
            }
          ],
          isshow: true
        },
        {
          index: "B",
          values: [
            {
              id: 1,
              title: "AALTO",
              imgurl: "http://placehold.it/300x200/1d00fa"
            },
            {
              id: 2,
              title: "AALTO",
              imgurl: "http://placehold.it/300x200/1d00fa"
            },
            {
              id: 3,
              title: "AALTO",
              imgurl: "http://placehold.it/300x200/1d00fa"
            },
            {
              id: 4,
              title: "AALTO",
              imgurl: "http://placehold.it/300x200/1d00fa"
            }
          ],
          isshow: true
        },
        {
          index: "C",
          values: [
            {
              id: 1,
              title: "AALTO",
              imgurl: "http://placehold.it/300x200/1d00fa"
            },
            {
              id: 2,
              title: "AALTO",
              imgurl: "http://placehold.it/300x200/1d00fa"
            },
            {
              id: 3,
              title: "AALTO",
              imgurl: "http://placehold.it/300x200/1d00fa"
            },
            {
              id: 4,
              title: "AALTO",
              imgurl: "http://placehold.it/300x200/1d00fa"
            }
          ],
          isshow: true
        }
      ]
    };
  },

  watch: {
    msg() {
      console.log("msg is changed");
    },
    values: {
      handler: function(val) {},
      deep: true
    }
  },

  created() {},
  mounted() {
    jquery("#app").scroll(this.handle_scroll);
  },

  methods: {
    handle_scroll() {
      this.activeIndex = "";
      for (let item in this.$refs) {
        let position = this.$refs[item][0]?.$el?.getBoundingClientRect();
        if (position?.top < 40) {
          this.activeIndex = item.split("_")[1];
        }
      }
    },
    jump_to_index(index) {
      let position = this.$refs[`collapseItem_${index}`][0]?.$el?.getBoundingClientRect();
      // this.$refs[`collapseItem_${index}`][0]?.$el.scrollIntoView();
      document.querySelector("#app").scrollTo({
        top: document.querySelector("#app").scrollTop + position.top - 40,
        behavior: "smooth"
      });
    }
  }
};
</script> 
  
<style lang="scss" scoped>
@import "~@/assets/css/theme/mixin.scss";

.index-list {
  position: relative;

  .index-anchor-top {
    position: fixed;
    top: 40px;
    background-color: #c3c3c3;
    height: 44px;
    width: 100vw;
    padding-left: 15px;
    line-height: 44px;
    font-size: 14px;
  }

  ::v-deep .van-collapse {
    .van-collapse-item {
      &.FPW {
        .van-collapse-item__title {
          background-color: rgb(195, 195, 195);
          @include themify {
            color: themed("primary-text-color");
          }
          font-weight: bold;
        }
      }

      &.HC {
        .van-collapse-item__title {
          @include themify {
            background-color: themed("primary-bg-color");
            color: themed("primary-text-color");
          }
          font-weight: bold;
        }
      }

      .van-collapse-item__content {
        padding: 20px 25px 10px 25px;
        background-color: #e9e8f6;
      }
    }

    .van-collapse-item--border::after {
      right: 0;
      left: 0;
    }

    .van-collapse-item__title::after {
      display: none;
    }
  }
}
</style> 