import Vue from "vue";
import Router from "vue-router";
import store from "@/store";
import Layout from "@/components/layout";
import Calender from "@/views/calender/index";
import Houses from "@/views/houses/index";
import HouseDetail from '@/views/house/index'

Vue.use(Router);

// let routes = [].concat(calenderRoute);

const router = new Router({
  routes: [
    {
      path: "/fashion",
      component: Layout,
      meta: {
        route: "calender",
        level: 1 //菜单级别
      },
      children: [
        {
          path: "",
          redirect: "calender"
        },
        {
          path: "calender",
          name: "calender",
          meta: {
            title: "Calender"
          },
          component: Calender
        },
        {
          path: "houses",
          name: "houses",
          meta: {
            title: "Houses"
          },
          component: Houses
        },
        {
          path: "house/:Hid",
          name: "house_detail",
          meta: {
            title: "HouseDetail"
          },
          component: HouseDetail
        }
      ]
    }
  ]
});

router.beforeEach((to, from, next) => {
  while (Vue.cancelTokenArr.length > 0) {
    Vue.cancelTokenArr.shift()();
  }

  next();
  // } else {
  //   if (to.fullPath == "/login") {
  //     next();
  //   } else {
  //     next("/login");
  //   }
  // }
});

export default router;
