<template>
    <transition mode="out-in"
        enter-active-class="animated slideInLeft faster"
        leave-active-class="animated slideOutLeft faster">
        <div v-if="visible"
            class="drawer">
            <div class="title">
                <span class="close"
                    @click="close">
                    <van-icon name="cross" /></span>
                <span class="text">FPW巴黎时装周</span>
            </div>
            <div class="tools">
                <van-search v-model="keywords"
                    placeholder="" />
            </div>
            <div class="content">
                <div class="logo">
                </div>
                <div class="menus">
                    <div @click="route_to_calender">日历</div>
                    <div @click="route_to_house_list">品牌列表</div>
                    <div @click="route_to_partners">合伙人</div>
                    <div @click="route_to_news">新闻</div>
                    <div @click="route_to_ecosystem">OUR ECOSYSTEM</div>
                </div>
            </div>
            <div class="socail-links">
                <span>
                    <i class="iconfont fs-weibo"></i>
                </span>
                <span>
                    <i class="iconfont fs-weixin"></i>
                </span>
                <span>
                    <i class="iconfont fs-kongjian"></i>
                </span>
            </div>
        </div>
    </transition>
</template> 
   
<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
import fetch from "@/services/fetch";

export default {
  name: "Drawer",
  components: {},

  props: {
    visible: {
      type: Boolean,
      required: false
    }
  },

  data() {
    return {
      keywords: "",
      logoUrl: ""
    };
  },

  computed: {
    // ...mapState("模块名称", ["state名称"]),
    // ...mapGetters("模块名称", ["getter名称"]),
    now() {
      return Date.now(); //不变
    }
  },

  /* 依赖注入， 本地通过this.injectName调用。 */

  // inject: ["injectName"],

  watch: {
    msg() {
      console.log("msg is changed");
    },
    values: {
      handler: function(val) {},
      deep: true
    }
  },

  created() {},
  mounted() {},

  methods: {
    // ...mapMutations("模块名称", ["mutation名称"]),
    // ...mapActions("模块名称", ["action名称"]),

    close() {
      this.$emit("update:visible", false);
    },

    route_to_calender() {
      this.$router
        .push({
          path: "/fashion/calender",
          query: { type: this.$getSplashType() }
        })
        .catch(() => {});
      this.$emit("update:visible", false);
    },
    route_to_house_list() {
      this.$router
        .push({
          path: "/fashion/houses",
          query: { type: this.$getSplashType() }
        })
        .catch(() => {});
      this.$emit("update:visible", false);
    },
    route_to_partners() {
      this.$emit("update:visible", false);
    },
    route_to_news() {
      this.$emit("update:visible", false);
    },
    route_to_ecosystem() {
      this.$emit("update:visible", false);
    }
  }
};
</script> 
  
<style lang="scss" scoped>
.drawer {
  position: fixed;
  top: 40px;
  width: 80vw;
  height: calc(100vh - 40px);
  background-color: #5d5dff;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;

  .title {
    height: 35px;
    display: flex;
    align-items: center;
    color: white;
    .close {
      font-size: 18px;
    }
    .text {
      flex: 1;
      text-align: center;
    }
  }

  .tools {
    .van-search {
      background-color: #5d5dff;
      .van-search__content {
        background-color: #000c81;
      }
    }
  }

  .content {
    flex: 1;
    display: flex;
    flex-direction: column;

    .logo {
      flex: 1;
      background-image: url("http://placehold.it/300x200/cc0b94");
      background-size: contain;
    }

    .menus {
      flex: 3;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      font-size: 26px;
      line-height: 45px;
      color: white;
    }
  }

  .socail-links {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      width: 50px;

      i {
        font-size: 28px;
      }
    }
  }
}
</style> 