import Drawer from "@/components/common/drawer";
import CountDown from "@/components/common/count-down";
import NextActivity from "@/components/common/next-activity";
import NextCalender from "@/components/common/next-calender";
import HouseListInline from "@/components/common/house-list-inline";

//作为插件必须定义公开的 install 方法
export default function install(Vue, options) {
  //注册全局组件
  Vue.component(Drawer.name, Drawer);
  Vue.component(CountDown.name, CountDown);
  Vue.component(NextActivity.name, NextActivity);
  Vue.component(NextCalender.name, NextCalender);
  Vue.component(HouseListInline.name, HouseListInline);
}
